<script>
  export default {
    data: () => {
      return {
        tab: 0,
        tamer: '',
        fullname: '',
        ombak: true,
        headers: [
          {
            sortable: false,
            text: 'ID',
            value: 'id',
          },
          {
            sortable: true,
            text: 'Mapel',
            value: 'mapel',
          },
          {
            sortable: false,
            text: 'Score required',
            value: 'nilailulus',
          },
          {
            sortable: false,
            text: 'Averge Student Score',
            value: 'avergescore',
          },
          {
            sortable: false,
            text: 'Student Score',
            value: 'score',
          },
          {
            sortable: false,
            text: 'Action',
            value: 'action',
          },
        ],
        hexa: [
          {
            sortable: false,
            text: 'ID',
            value: 'id',
          },
          {
            sortable: false,
            text: 'Semester',
            value: 'semester',
          },
          {
            sortable: true,
            text: 'Matematika',
            value: 'math',
          },
          {
            sortable: true,
            text: 'IPA',
            value: 'ipa',
          },
          {
            sortable: true,
            text: 'Bahasa Indonesia',
            value: 'indonesia',
          },
          {
            sortable: false,
            text: 'Action',
            value: 'action',
          },
        ],
        gap: [],
        pis: [],
        items: [],
        select : '',
        sl : '',
        sv : '',
        edit (item){
          this.editedIndex = this.pis.indexOf(item)
          this.editedItem = Object.assign({}, item)
          // this.dialog = true
            var x = document.querySelectorAll("tbody")[1].querySelectorAll("tr")
            x[item.id - 1].contentEditable = "true"
        },
        save (item){
            this.editedIndex = this.pis.indexOf(item)
            this.editedItem = Object.assign({}, item)
            // this.dialogDelete = true
            // Start collection
            var x = document.querySelectorAll("tbody")[1].querySelectorAll("tr")
              var y = x[item.id - 1].querySelectorAll("td")
              var m = y[2].innerText
              if (this.pis[item.id - 1].math !== m){
                this.pis[item.id - 1].math = y[2].innerText
                this.pis[item.id - 1].ipa = y[3].innerText
                this.pis[item.id - 1].indonesia = y[4].innerText
                var c = item.id
              setTimeout(() => {
                x[item.id - 1].contentEditable = "false"
                alert("ID : " + c +" saved")
              }, 300)
              }else{
                x[item.id -1].contentEditable = "false"
              }
        },
        delet (item){
          this.editedIndex = this.pis.indexOf(item)
          this.editedItem = Object.assign({}, item)
          // this.dialogDelete = true
          var x = document.querySelectorAll("tbody")[1].querySelectorAll("tr")
          x[item.id - 1].style.display = "none"
        },
        ed (item){
          this.editedIndex = this.gap.indexOf(item)
          this.editedItem = Object.assign({}, item)
          // this.dialog = true
            var x = document.querySelectorAll("tbody")[0].querySelectorAll("tr")
            x[item.id - 1].contentEditable = "true"
        },
        sa (item){
            this.editedIndex = this.gap.indexOf(item)
            this.editedItem = Object.assign({}, item)
            // this.dialogDelete = true
            // Start collection
            var x = document.querySelectorAll("tbody")[0].querySelectorAll("tr")
              var y = x[item.id - 1].querySelectorAll("td")
              var m = y[1].innerText
              if (this.gap[item.id - 1].mapel !== m){
                this.gap[item.id - 1].mapel = y[1].innerText
                this.gap[item.id - 1].nilailulus = y[2].innerText
                this.gap[item.id - 1].score = y[4].innerText
                var c = item.id
              setTimeout(() => {
                x[item.id - 1].contentEditable = "false"
                alert("ID : " + c +" saved")
              }, 300)
              }else if(this.gap[item.id - 1].score !== y[4].innerText){
                this.gap[item.id - 1].mapel = y[1].innerText
                this.gap[item.id - 1].nilailulus = y[2].innerText
                this.gap[item.id - 1].score = y[4].innerText
                var c = item.id
              setTimeout(() => {
                x[item.id - 1].contentEditable = "false"
                alert("ID : " + c +" saved")
              }, 300)
              }else{
                x[item.id -1].contentEditable = "false"
              }
        },
        de (item){
          this.editedIndex = this.gap.indexOf(item)
          this.editedItem = Object.assign({}, item)
          // this.dialogDelete = true
          var x = document.querySelectorAll("tbody")[0].querySelectorAll("tr")
          x[item.id - 1].style.display = "none"
        },
      }
    },
    methods: {
      sel(){
        const dat = JSON.parse(localStorage.getItem("user"))
        const val = this.select
        function red(log){
          return log.nama == val
        }
        const user = dat.filter(red)
        
        // Profile Siswa/i

        this.fullname = user[0].profile.fullname
        this.status = user[0].profile.status
        this.semester = user[0].profile.semester
        this.bio = user[0].profile.bio

        // Raport siswa/i
        
        let l
        this.sl = user[0].nilai
          this.sl.mapel.forEach((item, i) => {
            this.gap[i].id = i + 1
            this.gap[i].mapel = this.sl.mapel[i]
            this.gap[i].nilailulus = this.sl.nilailulus[i]
            this.gap[i].avergescore = this.sl.avergescore[i]
            this.gap[i].score = this.sl.score[i]
          })

        // Nilai Ujian siswa/i

        this.sv = user[0].ujian
        var data = [
          {
            id: 1,
            semester: 'I',
            ipa: this.sv.sem1.ipa,
            indonesia: this.sv.sem1.indonesia,
            math: this.sv.sem1.math,
          },
          {
            id: 2,
            semester: 'II',
            ipa: this.sv.sem2.ipa,
            indonesia: this.sv.sem2.indonesia,
            math: this.sv.sem2.math,
          }
        ]
        this.pis = data
        
      },
    },
    mounted(){

      var user = JSON.parse(localStorage.getItem("user"))

      // Profile Siswa/i

      this.fullname = user[0].profile.fullname
      this.status = user[0].profile.status
      this.semester = user[0].profile.semester
      this.bio = user[0].profile.bio

      // Raport siswa/i

       this.sl = user[0].nilai
        this.sl.mapel.forEach((item, i) => {
          var ite = [
            {
              id : i + 1,
              mapel : this.sl.mapel[i],
              nilailulus : this.sl.nilailulus[i],
              avergescore : this.sl.avergescore[i],
              score : this.sl.score[i],
            }
          ]
          this.gap = this.gap.concat(ite)
        })

      // Nilai Ujian siswa/i

      this.sv = user[0].ujian
      var data = [
        {
          id: 1,
          semester: 'I',
          ipa: this.sv.sem1.ipa,
          indonesia: this.sv.sem1.indonesia,
          math: this.sv.sem1.math,
        },
        {
          id: 2,
          semester: 'II',
          ipa: this.sv.sem2.ipa,
          indonesia: this.sv.sem2.indonesia,
          math: this.sv.sem2.math,
        }
      ]
      this.pis = this.pis.concat(data)
      
      // Name User

      user.forEach((item, i) => {
        this.items = this.items.concat(item.nama)
      })
      this.select = { nama : this.items[0]}
    }
  }
</script>
